@import "./style/_vars.scss"; @import "./style/_mixins.scss"; @import "./spritesmith-generated/sprite.scss";
@font-face {
  font-family: 'Tahoma';
  src: url('../fonts/Tahoma/Tahoma.eot');
  src: url('../fonts/Tahoma/Tahoma.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Tahoma/Tahoma.woff2') format('woff2'),
  url('../fonts/Tahoma/Tahoma.woff') format('woff'),
  url('../fonts/Tahoma/Tahoma.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Tahoma';
  src: url('../fonts/Tahoma/Tahoma-Bold.eot');
  src: url('../fonts/Tahoma/Tahoma-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Tahoma/Tahoma-Bold.woff2') format('woff2'),
  url('../fonts/Tahoma/Tahoma-Bold.woff') format('woff'),
  url('../fonts/Tahoma/Tahoma-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
